/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
}

.App .Panel {
  width: 100vw;
  height: 50vh;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-transition: background-color 1s ease;
  transition: background-color 1s ease;
}

.Panel .SocialLink a {
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}
.Panel .SocialLink a:hover,
.Panel .SocialLink a:focus {
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
}

.Panel:nth-of-type(even) .SocialLink a:hover,
.Panel:nth-of-type(even) .SocialLink a:focus {
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

.App > * > * {
  height: 100%;
  width: 100%;
}

.ColorChanger {
  position: fixed;
  top: 0;
  right: 0;
  width: 5rem;
  height: 5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0.9;
  -webkit-animation: pulse 2s ease-in-out 0s 3 alternate;
          animation: pulse 2s ease-in-out 0s 3 alternate;
}
.ColorChanger:hover,
.ColorChanger:focus {
  cursor: pointer;
  opacity: 1;
  outline: none;
  outline-width: 0;
  box-shadow: 2px 0 black;
}
.ColorChanger svg {
  -webkit-transition: fill 1s ease;
  transition: fill 1s ease;
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (min-width: 786px) {
  .App .Panel {
    width: 50vw;
    height: 100vh;
  }
}

:root {
