  --font--body: "Raleway", sans-serif;
  --mobile-nav-height: 80px;
  --breakpoint: 768px;

  --color1: #3d348b;
  --color2: #7678ed;
  --color3: #f7b801;
  --color4: #f18701;
  --color5: #f35b04;

